.form {
  width: 400px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  font-size: 20px;
  border: 2px solid #1565c0;
  border-radius: 8px;
  box-shadow: 0px 2px 11px -1px rgba(156, 158, 204, 1);
}

.label {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
